.zest-radio-wrapper {
    display: block;

    &.inline {
        display: inline-block;
        width: auto;
        margin-right: 18px;
    }

    .zest-radio-label {
        position: relative;
        padding-left: 25px;
        min-height: 18px;
        display: inline-block;
        font-weight: 400;

        &:hover {
            cursor: pointer;
        }

        &.disabled {
            > .zest-radio {
                background-color: #F5F5F5;

                &:after {
                    background: #CCCCCD;
                }
            }
        }

        > span {
            display: inline-block;
            line-height: 16px;
        }

        > input {
            position: absolute;
            top: 0;
            left: 0;
            width: 19px;
            height: 19px;
            z-index: 1;
            opacity: 0;
            outline: none;
        }

        > input:checked + .zest-radio {
            &:after {
                -webkit-transform: scale(1);
                -ms-transform: scale(1);
                -o-transform: scale(1);
                transform: scale(1);
            }
        }

        .zest-radio {

            &:before {
                top: 0;
                width: 16px;
                height: 16px;
                border-radius: 50%;
                left: 0;
                border: 1px solid #ccc;
                -webkit-transition: all;
                -o-transition: all;
                transition: all;
                -webkit-transition-duration: 250ms;
                transition-duration: 250ms;
                -webkit-backface-visibility: hidden;
                -moz-backface-visibility: hidden;
                backface-visibility: hidden;
                position: absolute;
                content: "";
            }

            &:after {
                -webkit-transition: all;
                -o-transition: all;
                transition: all;
                -webkit-transition-duration: 250ms;
                transition-duration: 250ms;
                -webkit-backface-visibility: hidden;
                -moz-backface-visibility: hidden;
                backface-visibility: hidden;
                position: absolute;
                content: "";
                width: 10px;
                height: 10px;
                background: #0099D8;
                border-radius: 50%;
                top: 3px;
                left: 3px;
                -webkit-transform: scale(0);
                -ms-transform: scale(0);
                -o-transform: scale(0);
                transform: scale(0);
            }
        }
    }
}

.radio-input-group {
    padding-left: 15px;

    > .zest-radio-wrapper {
        margin-left: -15px;

        & ~ * {
            display: block;
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}